.simp.button {
  background-color: #323232;
  color: #ffffff;
  display: inline-block;
  margin-bottom: 0.225rem;
  padding: 0.425em 1em;
  border: none;
  border-radius: 0.25em;
  cursor: pointer;
  transition: background-color 0.2s;
}

.simp.button:hover {
  background-color: #252525;
  color: #ffffff;
}

.simp.button.dark {
  background-color: #323232;
  color: #ffffff;
}

.simp.button.dark:hover {
  background-color: #252525;
  color: #ffffff;
}

.simp.button.primary {
  background-color: #2e86de;
  color: #ffffff;
}

.simp.button.primary:hover {
  background-color: #2179d1;
  color: #ffffff;
}

.simp.button.secondary {
  background-color: #576574;
  color: #ffffff;
}

.simp.button.secondary:hover {
  background-color: #4c5865;
  color: #ffffff;
}

.simp.button.idle, .simp.button[disabled] {
  background-color: #d9d9d9;
  color: #323232;
}

.simp.button.idle:hover, .simp.button[disabled]:hover {
  background-color: #cccccc;
  color: #323232;
}

.simp.button.success {
  background-color: #10ac84;
  color: #ffffff;
}

.simp.button.success:hover {
  background-color: #0e9572;
  color: #ffffff;
}

.simp.button.alert {
  background-color: #ee5253;
  color: #ffffff;
}

.simp.button.alert:hover {
  background-color: #ec3b3c;
  color: #ffffff;
}

.simp.button.warning {
  background-color: #ff9f43;
  color: #ffffff;
}

.simp.button.warning:hover {
  background-color: #ff922a;
  color: #ffffff;
}

.simp.button.highlight {
  background-color: #feca57;
  color: #ffffff;
}

.simp.button.highlight:hover {
  background-color: #fec23e;
  color: #ffffff;
}
